
* {
  margin: 0;
  padding: 0;
}

html,
body {
  overflow: hidden;
}

#gameScreen {
  position: fixed;
  top: 0;
  left: 0;
  outline: none;
}

#smallScreen {
  position: fixed;
  left: 20px;
  bottom: 20px;
}

.loading {
  text-align: center;
  position: fixed;
  font-size: 30px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(250, 234, 17);
  display: none;
}
.loading-text{
  text-align: center;
  margin: 50px;
  font-size: 30px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  position: fixed;
  top: 40%;
  left: 50%; 
  transform: translate(-50%, -50%);
  font-size: 50px;
}

.menu {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: bisque;
  display: none;
}

/* PLAY Button */


.menu-play {
  position: relative;
  background-color: #04AA6D;
  border: none;
  font-size: 28px;
  color: #FFFFFF;
  padding: 20px;
  width: 200px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  position: fixed;
    top: 50%;
    left: 35%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
}

.menu-play:after {
  content: "";
  background: #90EE90;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px!important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s
}

.menu-play:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s
}

/* Instruction button */

.menu-instruction {
  position: relative;
  background-color: #04AA6D;
  border: none;
  font-size: 28px;
  color: #FFFFFF;
  padding: 20px;
  width: 200px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  position: fixed;
    top: 50%;
    left: 65%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
}

.menu-instruction:after {
  content: "";
  background: #90EE90;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px!important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s
}

.mmenu-instruction:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s
}

